
.new_app {
  margin: 45px;
  width: 100%;
}
.new_app_header{
  width: 100%;
  display: inline;
}
.new_app_header button{
  background-color: #2e73bc;
  color: white;
  border: solid #2e73bc 1px ;
  border-radius: 3px;
  float: right;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 8px;
  padding-top: 3px;
}
.new_app_header button:hover{
  cursor: pointer;
}
.new_app_header button span{
  font-weight: 900;
  font-size: larger;
}
.applications{
  margin-top: 50px;
}



