
.input-field-header{
  background-color: #F8F8F8;
  width: 100%;
  padding: 15px;
  border: solid 1px #dddddd;
  text-align: left;
}
.input-field-header img{
  float: right;
}
.main_section.interface-main .entry{
  margin-bottom: 40px;
  margin-left:15px;
  margin-right: 15px;
}
.entry.boolean-selectors{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.entry.boolean-selectors div{
  margin-right: 60px;
}
.entry select{
  width:100%;
  height:30px;
}
.interface-main{
  border: solid 1px #dddddd;
  border-radius: 4px;
}




